.grid-wrapper {
    display: grid;
    grid-template-columns: minmax(1rem, 1fr) minmax(auto, 70ch) minmax(1rem, 1fr);
    grid-gap: 1rem;
}

input[type=text],
input[type=number] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 2px solid var(--primary-color);
    border-radius: 10px;
    box-sizing: border-box;
}

button {
    width: 100%;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 10px;
    border: 2px solid var(--primary-color);
    cursor: pointer;
}

button:hover {
    filter: brightness(0.85);
}